import React from "react"
import { Link, navigate } from "gatsby"
import { useForm } from "react-hook-form"
import { useState } from "react"
import json from "/src/siteData.json"
import Alert from "@mui/material/Alert"

const jobs = json.header.jobs

const Component = ({ location, pageContext }) => {
  const {
    register,
    handleSubmit,
    formState: { isValid, errors },
  } = useForm({
    mode: "onChange",
  })

  const [isThanks, setIsThanks] = useState(false)
  const [isConfirm, setIsConfirm] = useState(false)
  const [formValues, setFormValues] = useState({})
  const [alertMessage, setAlertMessage] = useState("")
  const [alertType, setAlertType] = useState("success")

  const step1 = !isThanks && !isConfirm
  const step2 = !isThanks && isConfirm

  const onSubmit = async data => {
    if (step1) {
      setFormValues(data)
      setIsConfirm(true)
      return
    }

    const formData = {
      inquiryType: `: ${data.name} ${data.hope}`,
      text: [
        { question: "", answer: data.name },
        { question: "", answer: data.age },
        { question: "", answer: data.gender },
        { question: "", answer: data.email },
        { question: "", answer: data.tel },
        { question: "", answer: data.job },
        { question: "", answer: `${data.zip} ${data.address}` },
        { question: "", answer: data.hope },
      ],
      inquiryDetails: data.contact,
    }

    try {
      const response = await fetch(pageContext.endpoint + "contact", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(formData),
      })
      if (!response.ok) {
        throw new Error(response.statusText)
      } else {
        // if (window.gtag) {
        //   window.gtag("event", "contact_form_submitted", {
        //     event_category: "Contact Form",
        //     event_label: "Submit",
        //     value: 1,
        //   })
        // }
        setIsConfirm(false)
        setIsThanks(true)
        navigate("/contact/thanks")
      }
    } catch (error) {
      console.log(error)
      setAlertMessage("")
      setAlertType("error")
      window.scrollTo({ top: 400, behavior: "smooth" })
    }
  }

  return (
    <div className="contactPage">
      {jobs && jobs.length && (
        <form onSubmit={handleSubmit(onSubmit)}>
          {Object.keys(errors).length > 0 && (
            <p className="errTop">
              
            </p>
          )}

          {step1 && (
            <p className="topTxt fadeInUp on">
              
            </p>
          )}

          {step2 && (
            <p className="topTxt fadeInUp on">
              
              <br className="sp" />
              
            </p>
          )}

          {alertMessage && (
            <Alert
              severity={alertType}
              style={{ marginBottom: "50px", fontSize: "15px" }}
            >
              {alertMessage}
            </Alert>
          )}

          {step1 && (
            <>
              <table className="fadeInUp on">
                <tbody>
                  <tr className={errors.name ? "error" : ""}>
                    <th>
                      <span className="must"></span>
                    </th>
                    <td>
                      <input
                        type="text"
                        size="40"
                        placeholder=""
                        {...register("name", {
                          required: true,
                          minLength: 1,
                        })}
                      />
                    </td>
                  </tr>
                  <tr className={errors.age ? "error" : ""}>
                    <th>
                      <span className="must"></span>
                    </th>
                    <td>
                      <input
                        type="number"
                        {...register("age", {
                          required: true,
                          min: 1,
                        })}
                      />
                    </td>
                  </tr>
                  <tr className={errors.gender ? "error" : ""}>
                    <th>
                      <span className="must"></span>
                    </th>
                    <td>
                      <select
                        {...register("gender", {
                          required: true,
                        })}
                      >
                        <option value="">---</option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                      </select>
                    </td>
                  </tr>
                  <tr className={errors.email ? "error" : ""}>
                    <th>
                      <span className="must"></span>
                    </th>
                    <td>
                      <input
                        type="email"
                        size="40"
                        placeholder=" ) info@example.jp"
                        {...register("email", {
                          required: true,
                          pattern: {
                            value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                          },
                        })}
                      />
                    </td>
                  </tr>
                  <tr className={errors.tel ? "error" : ""}>
                    <th>
                      <span className="must"></span>
                    </th>
                    <td>
                      <input
                        type="tel"
                        size="40"
                        maxlength="13"
                        minlength="10"
                        placeholder=" ) 0300000000"
                        {...register("tel", {
                          required: true,
                          pattern: {
                            value: /^[0-9]{10,13}$/,
                          },
                        })}
                      />
                    </td>
                  </tr>
                  <tr className={errors.job ? "error" : ""}>
                    <th>
                      <span className="must"></span>
                    </th>
                    <td>
                      <select
                        {...register("job", {
                          required: true,
                        })}
                      >
                        <option value="">---</option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                      </select>
                    </td>
                  </tr>
                  <tr className={errors.zip || errors.address ? "error" : ""}>
                    <th>
                      <span className="must"></span>
                    </th>
                    <td>
                      <p className="add">
                        <span className="ico"></span>
                        <span
                          className="fzip"
                          style={{
                            display: "inline-block",
                          }}
                        >
                          <input
                            type="text"
                            size="40"
                            placeholder="100-000"
                            {...register("zip", {
                              required: true,
                              pattern: {
                                value: /^[0-9]{3}-?[0-9]{4}$/,
                              },
                            })}
                          />
                        </span>
                      </p>
                      <p>
                        <input
                          type="text"
                          size="40"
                          placeholder=" ) "
                          {...register("address", {
                            required: true,
                          })}
                        />
                      </p>
                    </td>
                  </tr>
                  <tr className={errors.hope ? "error" : ""}>
                    <th>
                      <span className="must"></span>
                    </th>
                    <td>
                      <select
                        {...register("hope", {
                          required: true,
                        })}
                      >
                        <option value="">---</option>
                        {jobs.map((job, index) => (
                          <option key={index} value={job.title}>
                            {job.title}
                          </option>
                        ))}
                      </select>
                    </td>
                  </tr>
                  <tr className={errors.contact ? "error" : ""}>
                    <th>
                      <span className="optional"></span>
                    </th>
                    <td>
                      <textarea
                        cols="40"
                        rows="10"
                        placeholder=""
                        {...register("contact")}
                      ></textarea>
                    </td>
                  </tr>
                </tbody>
              </table>

              <p className="txtP fadeInUp">
                
                <Link to="/privacy" target="_blank">
                  
                </Link>
                
                <br className="sp" />
                
                <br />
                
              </p>
              <p className="agreeTxt fadeInUp">
                <span className="wpcf7-list-item">
                  <label>
                    <input
                      type="checkbox"
                      {...register("agree", {
                        required: true,
                      })}
                    />
                    <span className="wpcf7-list-item-label">
                      
                    </span>
                  </label>
                </span>
              </p>

              <ul className="submit fadeInUp">
                <li className={!isValid ? "disabled" : ""}>
                  <span className="info01">
                    <span className="info02">
                      <input
                        type="submit"
                        value=""
                        disabled={!isValid}
                      />
                    </span>
                  </span>
                </li>
              </ul>
            </>
          )}

          {step2 && (
            <>
              <table className="confirmTab fadeInUp on">
                <tbody>
                  <tr>
                    <th>
                      <span className="must"></span>
                    </th>
                    <td>
                      <span className="fname">{formValues.name}</span>
                    </td>
                  </tr>
                  <tr>
                    <th>
                      <span className="must"></span>
                    </th>
                    <td>
                      <span className="fage">{formValues.age}</span>
                    </td>
                  </tr>
                  <tr>
                    <th>
                      <span className="must"></span>
                    </th>
                    <td>
                      <span className="fsex">{formValues.gender}</span>
                    </td>
                  </tr>
                  <tr>
                    <th>
                      <span className="must"></span>
                    </th>
                    <td>
                      <span className="femail">{formValues.email}</span>
                    </td>
                  </tr>
                  <tr>
                    <th>
                      <span className="must"></span>
                    </th>
                    <td>
                      <span className="ftel">{formValues.tel}</span>
                    </td>
                  </tr>
                  <tr>
                    <th>
                      <span className="must"></span>
                    </th>
                    <td>
                      <span className="fjob">{formValues.job}</span>
                    </td>
                  </tr>
                  <tr>
                    <th>
                      <span className="must"></span>
                    </th>
                    <td>
                      <p className="add">
                        <span className="ico"></span>
                        <span className="fzip">{formValues.zip}</span>
                      </p>
                      <p>
                        <span className="faddress">{formValues.address}</span>
                      </p>
                    </td>
                  </tr>
                  <tr>
                    <th>
                      <span className="must"></span>
                    </th>
                    <td>
                      <span className="fhope">{formValues.hope}</span>
                    </td>
                  </tr>
                  <tr>
                    <th>
                      <span className="optional"></span>
                    </th>
                    <td>
                      <span className="fcontact">{formValues.contact}</span>
                    </td>
                  </tr>
                </tbody>
              </table>

              <ul className="submit fadeInUp on">
                <li className="back">
                  <span className="info01">
                    <span className="info02">
                      <input
                        type="button"
                        value=""
                        onClick={() => setIsConfirm(false)}
                      />
                    </span>
                  </span>
                </li>
                <li>
                  <span className="info01">
                    <span className="info02">
                      <input type="submit" value="" />
                    </span>
                  </span>
                </li>
              </ul>
            </>
          )}
        </form>
      )}
    </div>
  )
}

export default Component
